<!--
 * @Description: 抢车单详情
 * @Version: 1.0.0
 * @Autor: gitxinghui
 * @Date: 2020-12-05 02:04:26
 * @LastEditors: Please set LastEditors
 * @LastEditTime: 2021-01-13 23:19:46
-->
<template>
    <view class="order_car_info">
        <!-- tabbar -->
        <cu-custom :refresh="preRrefresh"><block slot="content">车单详情</block></cu-custom>
        <scroll-view class="order_car_info_scroll" :class="(orderInfo.user_release_cars.status >= 1 && orderInfo.user_release_cars.status < 5) || orderInfo.user_release_cars.status === 13 || orderInfo.user_release_cars.status === 4 || orderInfo.user_release_cars.status === 9 ? 'btn-padding-bottom':''" scroll-y="true">
            <!-- 订单状态 -->
            <view class="flex justify-center align-start order_type_info">
                <view class="type_text">{{orderInfo.user_release_cars.status | grabCarorder_format}}</view>
                <view class="type_tip_text">{{orderInfo.user_release_cars.status | grabCarOrderTip_format}}</view>
            </view>
            <!-- 订单信息 -->
            <view class="things_width_full order_info">
                <view class="order_single_info">姓名：{{orderInfo.users.nickname}}</view>
                <view class="order_single_info">货物名称：{{orderInfo.goods_name}}</view>
                <view class="things_width_full flex justify-start align-center order_double_info">
                    <view class="order_left_info">重量：{{orderInfo.weight}}吨</view>
                    <view class="order_right_info">损耗范围：{{orderInfo.loss_range}}</view>
                </view>
            </view>
            <!-- 货车信息 -->
            <view class="things_width_full car_info">
                <!-- 地址 -->
                <view class="flex justify-between align-center address_info">
                    <view class="address_info_left">
                        <view class="text-cut start_place">装车地点：{{orderInfo.loadingAddress}}</view>
                        <view class="text-cut arrive_place">卸车地点：{{orderInfo.unloadingAddress}}</view>
                    </view>
                    <view class="address_info_right" @tap="linkToGps">
                        <image class="gps_icon" src="./../../../../../../static/images/global/gps_icon.png" mode="widthFix" />
                    </view>
                </view>
                <view class="car_basic_info">
                    <view class="order_num">订单编号：{{orderInfo.user_release_cars.order_no}}</view>
                    <view class="things_width_full flex justify-start align-center user_info">
                        <view class="imageBox">
                            <image class="user_cover" :src="orderInfo.user_release_cars.users.avatarUrl" mode="widthFix"/>
                        </view>
                        <view class="name">{{orderInfo.user_release_cars.users.nickname}}</view>
                        <view class="flex justify-start align-center score">
                            <image class="score_icon" src="./../../../../../../static/images/global/score_icon.png" mode="widthFix" />
                            <view class="score_num">{{orderInfo.user_release_cars.users.reputation}}</view>
                            <view class="identity_text">{{orderInfo.user_release_cars.users.user_type}}</view>
                        </view>
                    </view>
                    <view class="things_width_full flex justify-between align-center car_double_info">
                            <view class="car_left_info">运输总价格：￥{{orderInfo.user_release_cars.total_money}}</view>
                            <view class="car_right_info construction_fee">建设费:￥{{orderInfo.user_release_cars.build_money}}</view>
                        </view>
                    <view class="car_single_info">车内灌质：{{orderInfo.user_release_cars.car_filling}}</view>
                    <view class="car_single_info">车辆类型：{{orderInfo.user_release_cars.car_type}}</view>
                    <view class="car_single_info">车牌号：{{orderInfo.user_release_cars.car_number}}</view>
                    <view class="car_single_info">挂车牌号：{{orderInfo.user_release_cars.listing_car_number}}</view>
                    <view class="car_single_info">罐体立方数：{{orderInfo.user_release_cars.cubic_number}}m³</view>
                    <view class="car_single_info">车辆所属公司：{{orderInfo.user_release_cars.car_company}}</view>
                    <view class="car_single_info">开始时间：{{orderInfo.user_release_cars.start_time}}</view>
                    <view class="car_single_info">结束时间：{{orderInfo.user_release_cars.end_time}}</view>
                    <view class="car_single_info">下单时间：{{orderInfo.user_release_cars.create_time}}</view>
                    <view class="car_single_info">备注：{{orderInfo.user_release_cars.remark}}</view>
                    <!-- 支付收据单据 -->
                    <view class="pay_msg" v-if="(orderInfo.user_release_cars.status >=5 && orderInfo.user_release_cars.status <= 12) || orderInfo.user_release_cars.status === 14">
                        <view class="pay_title">支付收据</view>
                        <view class="things_width_full receipt_list">
                            <image class="receipt_item" :src="orderInfo.user_release_cars.pay_pic.pic_path" :data-url="orderInfo.user_release_cars.pay_pic.pic_path" mode="widthFix" @tap="previewImage" />
                        </view>
                    </view>
                    <!-- 货车上传到达单据 -->
                    <view class="pay_msg" v-if="orderInfo.user_release_cars.status >= 9">
                        <view class="pay_title">收货单据</view>
                        <view class="car_single_info">损耗：{{orderInfo.user_release_cars.loss_weight}}</view>
                        <view class="things_width_full receipt_list">
                            <image class="receipt_item" :src="orderInfo.user_release_cars.transport_pic.pic_path" :data-url="orderInfo.user_release_cars.transport_pic.pic_path" mode="widthFix" @tap="previewImage" />
                        </view>
                    </view>
                </view>
            </view>
        </scroll-view>
        <!-- 按钮操作区域 -->
        <view class="things_width_full flex justify-end align-center btn_box" v-if="(orderInfo.user_release_cars.status >= 1 && orderInfo.user_release_cars.status < 5) || orderInfo.user_release_cars.status === 13 || orderInfo.user_release_cars.status === 4 || orderInfo.user_release_cars.status === 9">
            <!-- 待确认 -->
            <button class="bg-white cu-btn cancle_btn" :disabled="buttonType" v-if="orderInfo.user_release_cars.status >= 1 && orderInfo.user_release_cars.status < 5" :data-id="orderInfo.id" @tap="cancelOrderFun">取消订单</button>
            <!-- 上传支付单据 -->
            <button class="bg-white cu-btn submit_btn" :disabled="buttonType" v-if="orderInfo.user_release_cars.status === 4" :data-id="orderInfo.id" @tap="payCarsOrderFun">支付货款</button>
            <!-- 运输中 -->
            <button class="cu-btn bg-white cancel_btn" :disabled="buttonType" v-if="orderInfo.user_release_cars.status === 9" :data-id="orderInfo.id" @tap.stop="getCallFun">拒绝收货</button>
            <button class="bg-white cu-btn submit_btn" :disabled="buttonType" v-if="orderInfo.user_release_cars.status === 9" :data-id="orderInfo.id" @tap="confirmFun">确认完成</button>
            <!-- 已完成 -->
            <!-- <button class="bg-white cu-btn assess_btn" v-if="orderInfo.user_release_cars.status === 11" @tap="payCarsOrderFun">评价</button> -->
        </view>
        <!-- loading弹框组件 -->
		<popLinkDialog></popLinkDialog>
		<popLoading></popLoading>
		<popDialog></popDialog>
		<tipModal></tipModal>
		<!-- 登录 -->
		<login></login>
    </view>
</template>

<script>
import { getGrabsCarOrderDetail_api,cancelOrder_api,grabConfirmReceiptGrabsGoods_api } from '@/api/index.js';
	import { appData } from '@/utils/config.js';
	import { mapState, mapGetters, mapActions, mapMutations } from 'vuex';
	import storage from '@/utils/storage.js';
    import { identity_format } from '@/utils/filters.js';
    import checkForm from '@/utils/checkFormData.js';
    import { grabCarorder_format,grabCarOrderTip_format } from '@/utils/filters.js';
	let that = null;
export default {
    data() {
        return {
            appBaseData:null,//全局配置信息
            userInfo: {},
            order_id: '',
            orderInfo: {
                user_release_cars: {}
            },// 订单详情
            buttonType: false,
            preRrefresh: false,// 判断上一页是否要刷新
        }
    },
    watch: {
        '$store.getters.base_buttonType': function(val) {
            that.buttonType = val;
        },
        '$store.getters.user_token': function(val) {
            that.getCarOderInfo();
        },
    },
    // 过滤器
    filters: {
        // 订单状态
        grabCarorder_format,
        grabCarOrderTip_format
    },
    computed: {
        ...mapGetters(['base_cancelOrderType']) 
    },
    methods: {
        ...mapMutations([
            'modal',
            'base'
        ]),
        /**清空数据
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        clearPageFun(e) {
            that.getCarOderInfo();
        },
        /**跳转到gps
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        linkToGps(e) {
            // uni.navigateTo({
            //     url: `/pages/things/user/order/gps/index`
            // });
        },
        /**取消订单
         * @description: 
         * @param {*} e
         * @return {*}
         */
        cancelOrderFun(e) {
            that.buttonType = true;
            let params = {
                order_id: e.currentTarget.dataset.id,
                type: that.base_cancelOrderType[1].value,
            };
            cancelOrder_api(params,that).then(res=>{
                that.base({name:'refreshPage',value:true});
                that.modal({name:'popLinkDialog',value:{type:true,tipTitle:'提示',tipContent:'取消成功',linkUrl:1}});
                that.buttonType = false;
            }).catch(err=>{
                console.log(err);
                that.buttonType = false;
            })
        },
        /**跳转到支付
         * @description: 
         * @param {*} e
         * @return {*}
         */
        payCarsOrderFun(e) {
            that.buttonType = true;
            uni.navigateTo({
                url: `/pages/things/user/order/pay/index?order_id=${e.currentTarget.dataset.id}`,
                complete: function(res) {
                    that.buttonType = false;
                }
            });
        },
        /**拨打电话
         * @description: 
         * @param {*} e
         * @return {*}
         */
        getCallFun(e) {
            uni.makePhoneCall({
                phoneNumber: '15626236839'
            });
        },
        /**完成订单
         * @description: 
         * @param {*} e
         * @return {*}
         */
        confirmFun(e) {
            that.buttonType = true;
            let params = {
                grabs_order_id: e.currentTarget.dataset.id,
            };
            grabConfirmReceiptGrabsGoods_api(params,that).then(res=>{
                that.modal({name:'popTipModal',value:{type:true,tipTitle:'提示',tipContent:'确认完成'}});
                that.base({name:'refreshPage',value:true});
                that.buttonType = false;
                that.clearPageFun();
            }).catch(err=>{
                console.log(err);
                that.buttonType = false;
            })
        },
        /**抢车单详情
         * @description: 
         * @param {*} e
         * @return {*}
         */
        getCarOderInfo(e) {
            let params = {
                grabs_order_id: that.order_id
            };
            getGrabsCarOrderDetail_api(params,that).then(res=>{
                that.orderInfo = res;
            }).catch(err=>{
                console.log(err)
            })
        },
        /**图片预览
         * @description: 
         * @param {*} e
         * @return {*}
         */
        previewImage(e) {
            console.log(e);
            // 预览图片
            uni.previewImage({
                urls: [e.currentTarget.dataset.url],
                current: e.currentTarget.dataset.url
            });
        }
    },
    // 页面生命周期
    onLoad: function (options) {
        that = this;
        if (!options.order_id || options.order_id == ''|| options.order_id == 'undefined') {
            that.modal({name:'popLinkDialog',value:{type:true,tipTitle:'提示',tipContent:'该订单不存在',linkUrl:1}});
            return false;
        }
        that.order_id = options.order_id;
        that.getCarOderInfo();
    }
}
</script>

<style lang="scss" scoped>
    @import "./index";
</style>